export default {
    onLoad(option){
        if(option.shop_id) {
            this.shop_id = option.shop_id
        }
        this.getshopcates()
    },
    data(){
        return {
            shopType:[],
            shop_id:15
        }
    },
    methods: {
        getshopcates(){
            this.$http.getshopcates({shop_id:this.shop_id}).then(res=>{
                if(res.status == 200) {
                    this.shopType = res.data.cateres
                }
            })
        }
    },
}
